import Vue from 'vue'
import { Button, Input,Statistic ,Col,Row,Alert,Select,Icon,Table,Timeline} from 'ant-design-vue'
import Router from 'vue-router'
import Introduction from '@/components/Introduction'
import Main from '@/components/Main'

Vue.use(Router)
Vue.use(Button)
Vue.use(Input)
Vue.use(Statistic)
Vue.use(Col)
Vue.use(Row)
Vue.use(Alert)
Vue.use(Select)
Vue.use(Icon)
Vue.use(Table)
Vue.use(Timeline)
export default new Router({
  routes: [
    {
      path: '/introduction',
      name: 'Introduction',
      component: Introduction
    },
    {
      path: '/',
      name: 'Main',
      component: Main
    }
  ]
})



