<meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, minimum-scale=1, user-scalable=no"/>
<template>
  <div class="b">
    <div class='top'>
      <a-button icon='arrow-left' @click='back'/>

    </div>  
    <div >
      <div class="content">{{$t('desc.title')}}</div>

      <div class="title">{{$t('desc.title7')}}</div>
      <div class="content">{{$t('desc.content7')}}</div>

      <div class="title">{{$t('desc.title5')}}</div>
      <div class="buyTip">{{ $t("queryTitle.buyTip") }} </div>

      <div class="content">{{$t('desc.content5')}}</div>

      <div>
        <table>
          <tr>
            <th>days</th>
            <th>ratio</th>
          </tr>
          <tr>
            <td>1</td>
            <td>1:100</td>
          </tr>
          <tr>
            <td>2</td>
            <td>1:98</td>
          </tr>
          <tr>
            <td>3</td>
            <td>1:96.04</td>
          </tr>
          <tr>
            <td>4</td>
            <td>1:94.12</td>
          </tr>
          <tr>
            <td>n</td>
            <td>1:100*(0.98)<sup>n-1</sup></td>
          </tr>

        </table>
      </div>

      <div class="title">{{$t('planning.title')}}</div>

        <a-timeline mode="alternate">
          <a-timeline-item>
            <a-icon slot="dot" type="clock-circle-o" style="font-size: 16px;" />
            {{$t('planning.p1')}}
          </a-timeline-item>
          <a-timeline-item>{{$t('planning.p1_1')}}</a-timeline-item>
          <a-timeline-item>{{$t('planning.p2')}}</a-timeline-item>
          <a-timeline-item>{{$t('planning.p3')}}</a-timeline-item>
          <a-timeline-item>{{$t('planning.p4')}}</a-timeline-item>
        </a-timeline>

      <div class="title">{{$t('desc.title1')}}</div>
      <div class="content">{{$t('desc.content1')}}</div>

      <div class="title">{{$t('desc.title2')}}</div>
      <div class="content">{{$t('desc.content2')}}</div>


      <div class="title">{{$t('desc.title3')}}</div>
      <div class="content">{{$t('desc.content3')}}</div>


      <div class="title">{{$t('desc.title8')}}</div>
      <div class="content">{{$t('desc.content8')}}</div>

      <div class="title">{{$t('desc.title9')}}</div>
      <div class="content">{{$t('desc.content9')}}</div>

      <div class="title">{{$t('desc.title4')}}</div>
      <div class="content">{{$t('desc.content4')}}</div>


      <div class="title">{{$t('desc.title6')}}</div>
      <div class="content">{{$t('desc.content6')}}</div>
    </div>
  </div>

</template>

<script>

export default {
  name: 'Introduction',
  data () {
    return {
      
    }
  },
  methods:{
    back(){
      window.app.$router.push('/')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.top{
  display: flex;
  justify-content: space-between;
}
.b{
  padding: 1rem;
}
.title{
  font-size: 1.2rem;
  text-align:left;
  font-weight:bold;
  margin-top:20px;
}
.content{
  font-size: 0.9rem;
  margin-top: 5px;
  text-align:left;
}
.buyTip{
  color:red;
  text-align:left;
  font-size: 1rem;

}
</style>



