
<template>
  <div class="content">
    <div class="top" v-show="!isLoading">
      <a-select class="langs" defaultValue='en' :value="defalutlang" @change="handleLangChange">
        <a-select-option value="en"> English </a-select-option>
        <a-select-option value="vn"> Việt Nam </a-select-option>
        <a-select-option value="jp"> 日本 </a-select-option>
        <a-select-option value="cn"> 中文 </a-select-option>

      </a-select>
      <a href="./#/introduction" class="introduction">{{
        $t("queryTitle.introduction")
      }}</a>

      <div class="address">{{ address }}</div>
    </div>
    <a-statistic-countdown
        title="Countdown"
        :value="deadline"
        @finish="onDeadlineFinish"
        v-show="!isOpen&&!isLoading&&showCountdown"
        class="countdown"
      />
    <div v-show="isOpen&&!isLoading">  
      <div class="info">
        
        <a-row :gutter="20" type="flex">
          <a-col :flex="1">
            <a-statistic :title="$t('queryTitle.rate')" :value="rate" />
          </a-col>
          <a-col :flex="1">
            <a-statistic :title="$t('queryTitle.raise')" :value="paymented" />
          </a-col>
          <a-col :flex="1">
            <a-statistic :title="$t('queryTitle.todayLeft')" :value="todayLeft" />
          </a-col>
        </a-row>
      </div>
      <div class="buyTip2">{{ $t("queryTitle.usdtAmount") }}: {{usdtAmount}} USDT </div>
      <a-input-search
        v-model="buyByNumText"
        type="number"
        oninput="if(value>100)value=100;"
        :loading="buyByNumLoading"
        suffix="USDT"
        placeholder="can buy 1~100"
        size="large"
        @search="buy"
      >
        <a-button slot="enterButton" type="primary" class="buyBt">
          {{ $t("queryTitle.buy") }}
        </a-button>
      </a-input-search>
      <div class="buyTip">{{ $t("queryTitle.buyTip") }} </div>
      <div class='lmaddress'>LM:{{LMAddress}}</div>

    </div>
    <a-alert
      type="error"
      :message="buyByNumErrorMsg"
      banner
      v-show="buyByNumErrorMsgShow"
    />
    <img src="./../assets/bird.gif" class="bird"/>
    <img src="static/img/loading.gif" class="loading" v-show="isLoading"/>
    <img src="static/img/loading.gif" class="loading" v-show="isLoadingBuy"/>

  </div>
</template>

<script>
import CrowdsaleByTokenAbi from "../config/abi/CrowdsaleByTokenAbi.json";
import defaultAbi from "../config/abi/defaultAbi.json";

import * as ethers from "ethers";
import Web3Modal from "web3modal";

var provider;
var signer;
var contract;
var contractAddress = process.env.VUE_APP_CONTRACT;
var buyTokenContract;
var buyToken = process.env.VUE_APP_BUY_TOKEN;
var baseDecimals = ethers.BigNumber.from(10).pow(18);
var haveInjectProvider = true;
function sleep(ms) {
    return new Promise(reslove => setTimeout(reslove, ms))
}
export default {
  name: "Main",

  data() {
    return {
      address: "",
      rate: "",
      weiRaised: "",
      paymented: "",
      weiRaised: "",
      todayLeft: "",
      buyByNumLoading: false,
      buyByNumText: "",
      buyByNumErrorMsg: "",
      buyByNumErrorMsgShow: false,
      deadline:0,
      isOpen:false,
      isLoading:true,
      isLoadingBuy:false,
      defalutlang:'en',
      LMAddress:'',
      usdtAmount:'0',
      showCountdown:true,
    };
  },
  methods: {
    handleLangChange(value) {
      window.app.$i18n.locale = value;
      this.$data.defalutlang = value
    },
    onDeadlineFinish(){
      this.$data.isOpen = true
    },
    query: async function () {
      if (contract == undefined) {
        isLoading = false;
        return;
      }
      console.log(contract);
      let rate = await contract.getCurrentRate();
      console.log("have "+haveInjectProvider);
      let address = haveInjectProvider?await signer.getAddress():"";
      let paymented = haveInjectProvider?await contract.getPaymented(address):0;
      let weiRaised = await contract._weiRaised();
      let weiRaisedToday = await contract.getWeiRaisedToday();
      let maxBuyAmountToday = await contract.getMaxBuyAmountToday();
      let isOpen = await contract.isOpen();
      let openTime = await contract.openingTime()
      this.$data.LMAddress = await contract.soldToken();
      maxBuyAmountToday = maxBuyAmountToday.div(baseDecimals);
      if(haveInjectProvider){
        let d = await buyTokenContract.balanceOf(address)
        if(d.isZero()){
          this.$data.usdtAmount = '0'
        }else{
          this.$data.usdtAmount = d.div(baseDecimals)
        }
      }
      
      this.$data.deadline = openTime*1000;
      this.$data.isOpen = isOpen;
      this.$data.address = address;
      this.$data.rate = `1:${parseFloat(rate/100).toFixed(2)/1}`;
      this.$data.paymented = paymented.div(baseDecimals);
      this.$data.weiRaised = weiRaised.div(baseDecimals);
      this.$data.todayLeft = `${
        maxBuyAmountToday - weiRaisedToday.div(baseDecimals).mul(10000).div(rate).toString()
      }/${maxBuyAmountToday}LM`;
      this.$data.isLoading = false;
      this.$data.buyByNumLoading = false;
    },
    buy: async function () {
      this.$data.buyByNumLoading = true;
      try {
        let num = this.$data.buyByNumText;
        if(num&&num.length>0){
          await this.buyByNum(num);
        }else{
          this.$data.buyByNumLoading = false;
        }
      } catch (error) {
        console.log(error);
        this.$data.buyByNumLoading = false;

      }
    },
    buyByNum: async function (num) {
      this.$data.buyByNumErrorMsg = "";
      this.$data.buyByNumErrorMsgShow = false;
      try {
        let bNum = ethers.BigNumber.from(num).mul(baseDecimals);
        await this.checkApprove(bNum);
        let data = await contract.buyTokens(num);
      } catch (error) {
        console.log(error);
        if (error) {
          this.$data.buyByNumErrorMsg = error.data.message;
          this.$data.buyByNumErrorMsgShow = true;
          this.$data.buyByNumLoading = false;

        }
      }
    },
    checkApprove: async function (num) {
      let approveNum = await buyTokenContract.allowance(
        this.$data.address,
        contractAddress
      );
      if (approveNum.gte(num)) {
        return true;
      }
      let data = await buyTokenContract.approve(contractAddress, num);
      await sleep(1000)
      return false;
    },
    onComplete(data) {},
    initWallet: async function () {
      this.$data.buyByNumErrorMsg = "";
      this.$data.buyByNumErrorMsgShow = false;
      const providerOptions = {};

      const web3Modal = new Web3Modal({
        cacheProvider: true, // optional
        providerOptions, // required
      });
      console.log(web3Modal);
      if(web3Modal.providerController.injectedProvider==null){
        let url = "https://data-seed-prebsc-1-s3.binance.org:8545/";
        provider = new ethers.providers.JsonRpcProvider(url);
        haveInjectProvider = false;
      }else{
        const web3provider = await web3Modal.connect();
        web3provider.on("chainChanged", (chainId) => {
          console.log(chainId);
          this.initWallet();
        });
        if (web3provider.chainId != process.env.VUE_APP_CHAIN_ID) {
          this.$data.buyByNumErrorMsgShow = true;
          this.$data.buyByNumErrorMsg = this.$t("queryTitle.errorNetwork");
          return;
        }
        provider = new ethers.providers.Web3Provider(web3provider);
        web3provider.on("accountsChanged", function (accounts) {
                that.query();
              });
      }
      if(!haveInjectProvider){
        this.$data.buyByNumErrorMsg = "please open in Chrome with Metamask or DApp";
        this.$data.buyByNumErrorMsgShow = true;
        this.$data.isLoading = false;
        this.$data.showCountdown = false;
        return;
      }
      
      
      signer = provider.getSigner();
      contract = new ethers.Contract(
        contractAddress,
        CrowdsaleByTokenAbi,
        haveInjectProvider?signer:provider
      );
      buyTokenContract = new ethers.Contract(buyToken, defaultAbi, haveInjectProvider?signer:provider);

      let that = this;
      contract.on(
        "TokensPurchased",
        (purchaser, beneficiary, value, amount, paymented) => {
          if(that.$data.buyByNumLoading){
            if(beneficiary==that.$data.address){
              that.query();  
            }
            
          }else{
            that.query();
          }
        }
      );
      
      that.query();
    },
  },
  mounted() {
    let that = this;
    this.$nextTick(function () {
      that.$data.defalutlang = window.app.$i18n.locale
      that.initWallet();
    });
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #000;
}
.introduction {
  flex: 1;
  text-align: center;
}
.content {
  padding: 1rem;
  width: 100%;
  height: 100%;
  background-image: url("~@/../static/img/bg.jpg");

}
.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.langs {
  margin-top: 0px;
  margin-right: auto;
  align-self: flex-start;
}
.address {
  border-radius: 15px;
  border: 2px solid rgb(156, 158, 160);
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 10px;
  padding-right: 10px;
  flex: 1;
  text-align: center;
  align-items: center;
}

.info {
  margin-top: 40px;
  margin-bottom: 30px;
}

.bird{
  z-index: 9999;
  width: 100%;
  height: 50%;
  margin-bottom: 30%;
}

.loading{
  width: 100%;
  position:absolute;
  z-index: 9999;
  left: 0%;
  top: 30%;
}

.buyBt{
  background-color: rgb(34, 62, 90);
}

.countdown{
  margin-top: 20%;
}
.buyTip{
  font-size: 0.8rem;
  color: darkgrey;
}
.buyTip2{
  font-size: 0.8rem;
  color: darkgrey;
  margin-right: 0;
  margin-left: auto;
  text-align: right;
}
.lmaddress{
  width: 90%;
  flex-wrap: wrap;
  font-size: 0.6rem;
  margin-top: 10px;
}
</style>



